import { TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useEffect, useRef, useState } from "react";
import { GeolocationControl, Map, SearchControl } from "react-yandex-maps";
import { makeStyles } from "@mui/styles";
import locationImg from "../../../../assets/images/dashboardHome/location.svg";

const FirstStep = ({ formik }) => {
  const [ymaps, setYmaps] = useState(false);

  const searchRef = useRef(null);

  useEffect(() => {
    if (ymaps)
      if (formik.values.geo)
        ymaps
          ?.geocode(formik.values.geo)
          ?.then((result) =>
            formik.setFieldValue(
              "destinationFrom",
              result.geoObjects.get(0).getAddressLine()
            )
          );
  }, [formik.values.geo]);

  return (
    <>
      <Typography variant="h6" mb={3}>
        Адрес отправки
      </Typography>
      <Box
        sx={{
          height: 600,
          overflow: "hidden",
          position: "relative",
          width: { xs: "100%", lg: "80%" },
        }}
        mb={{ xs: 2, lg: 5 }}
      >
        <Map
          modules={["geolocation", "geocode"]}
          defaultState={{
            center: [42.869527, 74.610654],
            zoom: 9,
          }}
          width="100%"
          height="100%"
          onLoad={(ymaps) => setYmaps(ymaps)}
        >
          <SearchControl
            instanceRef={(ref) => {
              if (ref) searchRef.current = ref;
            }}
            options={{ float: "right" }}
          />
          <GeolocationControl
            options={{ float: "right" }}
            onLocationChange={(e) => {
              formik.setFieldValue("geo", e.get("position"));
            }}
            data={{
              image: locationImg,
              title: "Укажите ваше точное местоположение",
            }}
          />
        </Map>
      </Box>
      <Box
        sx={{
          width: { xs: "100%", lg: "80%" },
        }}
      >
        <TextField
          fullWidth
          name="destinationFrom"
          required
          value={formik.values.destinationFrom}
          onChange={formik.handleChange}
          variant="outlined"
          placeholder="Улица, дом квартира (офис)"
        />

        <TextField
          sx={{ mt: 2 }}
          fullWidth
          name="fromDescr"
          value={formik.values.fromDescr}
          onChange={formik.handleChange}
          variant="outlined"
          placeholder="Примечание"
          multiline
          rows={4}
        />
      </Box>
    </>
  );
};

export default FirstStep;

const useStyles = makeStyles(() => ({}));
