import { InputAdornment, TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { CustomMask } from "../../../components/CustomMask";
import EditIcon from "./EditIcon";

const Seventh = ({ formik }) => {
  const { account } = useSelector(({ account }) => account);

  const [disabled, setDisabled] = useState({
    info: true,
    phone: true,
  });

  useEffect(() => {
    formik.setFieldValue("senderInfo", account.info);
    formik.setFieldValue("senderPhone", account.phone);
  }, [account]);

  return (
    <>
      <Typography variant="h6" mb={3}>Мои данные</Typography>
      <Box
        display="flex"
        flexDirection="column"
        sx={{ width: { xs: "100%", lg: "370px" } }}
      >
        <TextField
          onChange={formik.handleChange}
          name="senderInfo"
          disabled={disabled.info}
          value={formik.values.senderInfo}
          fullWidth
          required
          placeholder="Имя Фамилия"
          InputProps={{
            endAdornment: (
              <InputAdornment
                position="end"
                onClick={() => setDisabled({ ...disabled, info: false })}
              >
                <EditIcon />
              </InputAdornment>
            ),
          }}
          variant="outlined"
        />

        <TextField
          onChange={formik.handleChange}
          name="senderPhone"
          id="+996 (000) 00 00 00"
          value={formik.values.senderPhone}
          disabled={disabled.phone}
          required
          fullWidth
          placeholder="Номер телефона"
          InputProps={{
            inputComponent: CustomMask,
            endAdornment: (
              <InputAdornment
                position="end"
                onClick={() => setDisabled({ ...disabled, phone: false })}
              >
                <EditIcon />
              </InputAdornment>
            ),
          }}
          variant="outlined"
          sx={{ mt: 2 }}
        />

        
        <TextField
          onChange={formik.handleChange}
          name="senderCompany"
          value={formik.values.senderCompany}
          fullWidth
          placeholder="Название компании"
          variant="outlined"
          sx={{ mt: 2 }}
        />
        <TextField
          onChange={formik.handleChange}
          type="email"
          name="senderEmail"
          value={formik.values.senderEmail}
          fullWidth
          placeholder="E-mail"
          variant="outlined"
          sx={{ mt: 2 }}
        />
      </Box>
    </>
  );
};

export default Seventh;
