import {
  Box,
  Paper,
  Typography,
  Button,
  Grid,
  useMediaQuery,
} from "@mui/material";
import { useTheme } from "@mui/styles";
import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useNavigate, useLocation } from "react-router";
import CheckMarkIcon from "../../../components/CheckMarkIcon";
import { setOrderData } from "../../../redux/reducers/ordering";

const Finish = ({ formik, setStep }) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const location = useLocation();
  const theme = useTheme();
  const md = useMediaQuery(theme.breakpoints.down("md"));

  const { order_data } = useSelector(({ ordering }) => ordering);

  useEffect(() => {
    dispatch(
      setOrderData({
        ...order_data,
        severity: null,
        open: false,
      })
    );
  }, []);

  return (
    <Box
      display="flex"
      alignItems="center"
      justifyContent="center"
      height="100%"
    >
      <Paper
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
          maxWidth: 870,
          width: "100%",
          background: "#FFFFFF",
          boxShadow: "0px 4px 58px rgba(0, 0, 0, 0.15)",
          borderRadius: "15px",
          padding: "50px",
        }}
      >
        <CheckMarkIcon finish />
        <Typography variant="h3" mt={2} sx={{ textAlign: "center" }}>
          Вы успешно оформили заказ,
          {!md && <br />} ожидайте курьера
        </Typography>
        <Grid
          display="flex"
          mt={5}
          container
          mb={5}
          justifyContent="space-between"
          width="100%"
          spacing={md ? 0 : 2}
        >
          <Grid item xs={12} lg={6}>
            <Typography
              variant="body1"
              sx={{ fontWeight: 500, mb: { xs: 1, lg: 2 } }}
            >
              Итоговая стоимость: {order_data?.data?.payment?.price} сом
            </Typography>
            {formik.values.bonus && (
              <Typography
                variant="body1"
                sx={{ fontWeight: 500, mb: { xs: 1, lg: 2 } }}
              >
                Остаток: {formik.values.amount} сом
              </Typography>
            )}
            <Typography
              variant="body1"
              sx={{ fontWeight: 500, mb: { xs: 1, lg: 0 } }}
            >
              Cтатус платежа: {order_data?.data?.payment?.pay_status}
            </Typography>
          </Grid>
          <Grid item xs={12} lg={6}>
            <Typography
              variant="body1"
              sx={{ fontWeight: 500, mb: { xs: 1, lg: 2 } }}
            >
              <b>Номер заказа: {order_data?.data?.code}</b>
            </Typography>
            <Typography variant="body1" sx={{ fontWeight: 500 }}>
              Дата сдачи груза:{" "}
              {new Date(order_data?.data?.sending_date).toLocaleDateString()}{" "}
              {new Date(order_data?.data?.sending_date).toLocaleTimeString([], {
                timeStyle: "short",
              })}
            </Typography>
          </Grid>
        </Grid>
        <Button
          variant="contained"
          size="large"
          onClick={() => {
            navigate("/dashboard");
            formik.resetForm();
          }}
          color="primary"
        >
          Перейти на главную
        </Button>
      </Paper>
    </Box>
  );
};

export default Finish;
