import React, { useEffect, useState, useLayoutEffect, useRef } from "react";
import {
  Alert,
  Box,
  Checkbox,
  Grid,
  ListItem,
  Paper,
  Snackbar,
  Typography,
} from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { setTermsOfUseDialog } from "../../../../redux/reducers/main";
import DialogBonus from "./DialogBonus";
import {
  setOrderData,
  setPaymentData,
} from "../../../../redux/reducers/ordering";

const Payment = ({ formik, setStep, step }) => {
  const dispatch = useDispatch();

  const [dialog, setDialog] = useState(false);

  const { payment_types, order_data, payment_data } = useSelector(
    ({ ordering }) => ordering
  );

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const firstUpdate = useRef(true);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    dispatch(
      setOrderData({
        ...order_data,
        severity: null,
        open: false,
      })
    );
  }, [step]);

  return (
    <>
      <Snackbar
        open={payment_data.open}
        autoHideDuration={2000}
        onClose={() =>
          dispatch(
            setPaymentData({
              ...payment_data,
              open: false,
              severity: null,
            })
          )
        }
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert
          severity={payment_data.severity === 200 ? "success" : "error"}
          color={payment_data.severity === 200 ? "success" : "error"}
          variant="filled"
        >
          {payment_data.severity === 200
            ? "Оплата бонусной картой прошла успешно"
            : "Что-то пошло не так"}
        </Alert>
      </Snackbar>
      <DialogBonus open={dialog} setOpen={setDialog} formik={formik} />
      <Box
        display="flex"
        alignItems="center"
        justifyContent="center"
        height="100%"
      >
        <Paper
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
            maxWidth: 870,
            background: "#FFFFFF",
            boxShadow: "0px 4px 58px rgba(0, 0, 0, 0.15)",
            borderRadius: "15px",
            padding: "50px",
          }}
        >
          <Typography
            variant="h3"
            sx={{
              textAlign: "center",
              mb: 6,
            }}
          >
            Выберите способ оплаты
          </Typography>
          <Grid container spacing={3}>
            {payment_types.map((item) => (
              <Grid
                item
                xs={6}
                key={item.title}
                onClick={() => {
                  if (item.type === "bonus") {
                    setDialog(true);
                    formik.setFieldValue("payment", 1);
                  } else setStep(step + 1);
                }}
                component={item.type !== "bonus" ? "button" : ""}
                lg={4}
                sx={{
                  cursor: "pointer",
                  flexDirection: "column",
                  border: "none",
                  background: "none",
                }}
                display="flex"
                alignItems="center"
                justifyContent="center"
              >
                <ListItem
                  button
                  sx={{
                    display: "flex",
                    flexDirection: "column",
                  }}
                >
                  <img
                    src={item.icon}
                    style={{ maxWidth: "100%", maxHeight: "64px" }}
                    alt=""
                  />
                  <Typography variant="h6" mt={2}>
                    {item.title}
                  </Typography>
                </ListItem>
              </Grid>
            ))}
          </Grid>
          <Box
            component="label"
            display="flex"
            alignItems="center"
            sx={{ mt: { xs: 0.5, md: 1 } }}
          >
            <Checkbox
              color="primary"
              required
              name="paymentConfirmation"
              value={1}
              onChange={formik.handleChange}
            />
            <Typography variant="subtitle1" component="span">
              Соглашение с{" "}
              <span
                className="link"
                onClick={() => dispatch(setTermsOfUseDialog(true))}
              >
                политикой возврата денежных средств*
              </span>
            </Typography>
          </Box>
        </Paper>
      </Box>
    </>
  );
};

export default Payment;
