import { Alert, Grid, Snackbar, Typography } from "@mui/material";
import { makeStyles } from "@mui/styles";
import { Box } from "@mui/system";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setOrderData } from "../../../redux/reducers/ordering";

const Eighth = ({ formik }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { regions } = useSelector(({ account }) => account);
  const { order_data } = useSelector(({ ordering }) => ordering);

  useEffect(() => {
    const regionTo = regions?.data
      ?.map((region) =>
        region.district_set.filter(
          (district) => district.id === formik.values.from
        )
      )
      .filter((item) => Boolean(item.length));
    const regionFrom = regions?.data
      ?.map((region) =>
        region.district_set.filter(
          (district) => district.id === formik.values.to
        )
      )
      .filter((item) => Boolean(item.length));

    formik.setFieldValue("regionTo", regionTo && regionTo[0] && regionTo[0][0]);
    formik.setFieldValue(
      "regionFrom",
      regionFrom && regionFrom[0] && regionFrom[0][0]
    );
  }, [formik.values.from, formik.values.to]);

  return (
    <>
      <Snackbar
        open={order_data.open && order_data?.severity !== 201}
        autoHideDuration={2000}
        onClose={() =>
          dispatch(
            setOrderData({
              ...order_data,
              severity: null,
              open: false,
            })
          )
        }
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert severity="error" variant="filled">
          Что-то пошло не так
        </Alert>
      </Snackbar>
      <Typography variant="h5" mt={2} mb={5}>
        Итоговая стоимость: {formik.values.price} сом
      </Typography>
      <Grid container spacing={4}>
        <Grid item xs={12} lg={6}>
          <Typography variant="h6" mb={2}>
            Общее
          </Typography>
          <Box className={classes.paper}>
            <Typography variant="body2">Откуда - Куда</Typography>
            <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
              {formik.values.regionTo.region}, {formik.values.regionTo.name} -{" "}
              {formik.values.regionFrom.region}, {formik.values.regionFrom.name}
            </Typography>
            <Typography variant="body2" sx={{ mt: { xs: 3, lg: 5 } }}>
              Параметры груза
            </Typography>
            <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
              {formik.values.envelop ? (
                formik.values.envelop
              ) : (
                <>
                  {`Вес (в кг) ${formik.values.weight}, Габариты ${formik.values.width}х${formik.values.length}х${formik.values.height} (в см)`}
                </>
              )}
            </Typography>
            <Typography variant="body2" sx={{ mt: { xs: 3, lg: 5 } }}>
              Тип доставки
            </Typography>
            <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
              {formik.values.delivery_type}
            </Typography>
          </Box>
        </Grid>
        <Grid item xs={12} lg={6}>
          <Typography variant="h6" mb={2}>
            Данные посылки
          </Typography>
          <Box className={classes.paper}>
            <Typography variant="body2">Упаковка</Typography>
            <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
              {formik.values.packaging}
            </Typography>
            <Typography
              variant="body2"
              sx={{ mt: { xs: 3, lg: 5 }, fontWeight: 600, color: "red" }}
            >
              Дата сдачи груза
            </Typography>
            <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
              {formik.values.sending_date}
            </Typography>
            <Typography variant="body2" sx={{ mt: { xs: 3, lg: 5 } }}>
              Дата доставки
            </Typography>
            <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
              {formik.values.delivery_type === "Авиа"
                ? "В течении одного рабочего дня после отправки"
                : "В течении нескольких рабочих дней после отправки"}
            </Typography>
          </Box>
        </Grid>
        <Grid item xs={12} lg={6}>
          <Typography variant="h6" mb={2}>
            Данные отправителя
          </Typography>
          <Box className={classes.paper}>
            <Typography variant="body2">Имя</Typography>
            <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
              {formik.values.senderInfo}
            </Typography>
            <Typography variant="body2" sx={{ mt: { xs: 3, lg: 5 } }}>
              Телефон
            </Typography>
            <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
              {formik.values.senderPhone}
            </Typography>
            {formik.values.senderEmail && (
              <>
                <Typography variant="body2" sx={{ mt: { xs: 3, lg: 5 } }}>
                  E-mail
                </Typography>
                <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
                  {formik.values.senderEmail}
                </Typography>
              </>
            )}
            {formik.values.senderCompany && (
              <>
                <Typography variant="body2" sx={{ mt: { xs: 3, lg: 5 } }}>
                  Название компании
                </Typography>
                <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
                  {formik.values.senderCompany}
                </Typography>
              </>
            )}
            {formik.values.destinationFrom && (
              <>
                <Typography variant="body2" sx={{ mt: { xs: 3, lg: 5 } }}>
                  Пункт отправки
                </Typography>
                <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
                  {formik.values.destinationFrom}
                </Typography>
              </>
            )}
          </Box>
        </Grid>
        <Grid item xs={12} lg={6}>
          <Typography variant="h6" mb={2}>
            Данные получателя
          </Typography>
          <Box className={classes.paper}>
            <Typography variant="body2">Имя</Typography>
            <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
              {formik.values.recipientInfo}
            </Typography>
            <Typography variant="body2" sx={{ mt: { xs: 3, lg: 5 } }}>
              Телефон
            </Typography>
            <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
              {formik.values.recipientPhone}
            </Typography>
            {formik.values.recipientEmail && (
              <>
                <Typography variant="body2" sx={{ mt: { xs: 3, lg: 5 } }}>
                  E-mail
                </Typography>
                <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
                  {formik.values.recipientEmail}
                </Typography>
              </>
            )}
            {formik.values.recipientCompany && (
              <>
                <Typography variant="body2" sx={{ mt: { xs: 3, lg: 5 } }}>
                  Название компании
                </Typography>
                <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
                  {formik.values.recipientCompany}
                </Typography>
              </>
            )}
            {formik.values.destinationTo && (
              <>
                <Typography variant="body2" sx={{ mt: { xs: 3, lg: 5 } }}>
                  Пункт получения
                </Typography>
                <Typography variant="body2" sx={{ fontWeight: "300" }} mt={1}>
                  {formik.values.destinationTo}
                </Typography>
              </>
            )}
          </Box>
        </Grid>
      </Grid>
    </>
  );
};

export default Eighth;

const useStyles = makeStyles(() => ({
  paper: {
    padding: 30,
    background: "#FFFFFF",
    border: "1px solid #C8C8C8",
    borderRadius: 20,
  },
}));
