import { Checkbox, FormControlLabel, Typography } from "@mui/material";
import { Box } from "@mui/system";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Size } from "../../../components/Forms";
import { setTermsOfUseDialog } from "../../../redux/reducers/main";
import Dropzone from "./Dropzone";

const Second = ({ formik, files, setFiles }) => {
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);

  const thumbs = files.map((file) => (
    <img
      src={file.preview}
      style={{
        width: "88%",
        height: 255,
        borderRadius: "8px",
        objectFit: "contain",
      }}
      alt=""
    />
  ));

  const { options } = useSelector(({ ordering }) => ordering);

  return (
    <>
      <Typography variant="h6">Параметры груза</Typography>
      <Typography
        variant="body2"
        mt={2}
        mb={5}
        sx={{
          fontWeight: "400",
        }}
      >
        Выберите размер из предложенного или введите точные параметры
      </Typography>
      <Size formik={formik} setOpen={setOpen} open={open} />
      <Box display="flex" flexDirection="column">
        <Box
          component="label"
          display="flex"
          alignItems="center"
          sx={{
            mt: { xs: 0.5, md: 1 },
            "& .MuiCheckbox-root": {
              padding: "9px 9px 9px 0",
            },
          }}
        >
          <Checkbox
            color="primary"
            required
            name="option"
            value={6}
            onChange={formik.handleChange}
          />
          <Typography variant="body1" component="span">
            Соглашение с{" "}
            <span
              onClick={() => dispatch(setTermsOfUseDialog(true))}
              className="link"
            >
              условиями доставки*
            </span>
          </Typography>
        </Box>
        {Array.isArray(options) &&
          options
            ?.filter((item) => !item.title.includes("Соглашение"))
            .map((item) => (
              <FormControlLabel
                key={item.id}
                sx={{ mt: { xs: 0.5, md: 1 } }}
                label={item.title}
                name="option"
                value={item.id}
                onChange={formik.handleChange}
                control={<Checkbox color="primary" />}
              />
            ))}
      </Box>
      <Typography variant="h6" mt={3} mb={2}>
        Прикрепите фотографию посылки
      </Typography>
      <Dropzone setFiles={setFiles} files={files} thumbs={thumbs} />
    </>
  );
};

export default Second;
