import { Button, Typography } from "@mui/material";
import { makeStyles } from "@mui/styles";
import { Box } from "@mui/system";
import React, { useEffect } from "react";
import Fifth from "./Fifth";
import Finish from "./Finish";
import FirstStep from "./FirstStep";
import Fourth from "./Fourth";
import Payment from "./Payment";
import Second from "./Second";
import Seventh from "./Seventh";
import Sixth from "./Sixth";
import Third from "./Third";
import Eighth from "./Eighth";
import { useDispatch, useSelector } from "react-redux";
import {
  getDeliveryTypes,
  getOptions,
  getPackaging,
  getPaymentTypes,
} from "../../../redux/reducers/ordering";
import Preloader from "../../../components/Preloader";

const Ordering = ({ setStep, step, formik, loading, setFiles, files }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getOptions());
    dispatch(getDeliveryTypes());
    dispatch(getPackaging());
    dispatch(getPaymentTypes());
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [step]);

  const { order_data } = useSelector(({ ordering }) => ordering);
  return (
    <>
      {loading && <Preloader />}
      <section className={classes.section}>
        {step < 9 && (
          <>
            <Typography variant="h3" sx={{ fontWeight: "600" }}>
              Оформить заказ
            </Typography>

            <Box
              display="flex"
              justifyContent="space-between"
              alignItems="center"
              sx={{ mt: { xs: 2, lg: 5 }, mb: { xs: 4, lg: 5 } }}
            ></Box>

            <Box
              sx={{
                borderBottom: "1px solid #C7C7C7",
                paddingBottom: { xs: 4, lg: 5 },
                overflow: "scroll",
                marginBottom: { xs: 4, lg: 5 },
                display: "flex",
                flexWrap: "nowrap",
              }}
            >
              {steps.map((item) => (
                <Typography
                  variant="subtitle1"
                  key={item.txt}
                  className={
                    step === item.id
                      ? `${classes.stepLabel} yellow`
                      : classes.stepLabel
                  }
                  component="span"
                  sx={{ position: "relative" }}
                  mr={6}
                >
                  {item.txt}
                </Typography>
              ))}
            </Box>
          </>
        )}

        <form action="" onSubmit={formik.handleSubmit}>
          {step === 1 && <FirstStep formik={formik} />}
          {step === 2 && (
            <Second formik={formik} files={files} setFiles={setFiles} />
          )}
          {step === 3 && <Third formik={formik} />}
          {step === 4 && <Fourth formik={formik} />}
          {step === 5 && <Fifth formik={formik} />}
          {step === 6 && <Sixth formik={formik} />}
          {step === 7 && <Seventh formik={formik} />}
          {step === 8 && <Eighth formik={formik} />}
          {step === 9 && (
            <Payment setStep={setStep} formik={formik} step={step} />
          )}
          {step === 10 && <Finish setStep={setStep} formik={formik} />}

          {step < 9 && (
            <Box display="flex" mt={5}>
              <Button
                variant="contained"
                size="large"
                onClick={() => setStep(step - 1)}
                // eslint-disable-next-line
                disabled={step == 1}
                sx={{ background: "#C4C4C4", width: 270, mr: 3 }}
              >
                Назад
              </Button>
              {step === steps.length + 1 && (
                <Button
                  variant="contained"
                  size="large"
                  color="primary"
                  type="submit"
                  sx={{ width: 270, mr: 3 }}
                >
                  Подтвердить заказ
                </Button>
              )}
              {step !== steps.length + 1 && (
                <Button
                  variant="contained"
                  size="large"
                  color="primary"
                  type="submit"
                  disabled={step > steps.length}
                  sx={{ width: 270 }}
                >
                  Далее
                </Button>
              )}
            </Box>
          )}
        </form>
      </section>
    </>
  );
};

export default Ordering;

const useStyles = makeStyles((theme) => ({
  stepLabel: {
    whiteSpace: "nowrap",
    fontSize: "14px!important",
    "&::before": {
      content: `''!important`,
      position: "absolute",
      top: "50%",
      transform: "translateY(-50%)",
      right: -24,
      height: 4,
      width: 4,
      backgroundColor: "#919BA8",
      borderRadius: "50%",
    },
  },
  section: {
    height: "100%",

    "& .MuiFormControlLabel-label": {
      fontWeight: 500,
      fontSize: 16,
      lineHeight: "16px",
      color: "#000000",

      [theme.breakpoints.down("md")]: {
        fontSize: 14,
      },
    },
  },
}));

const steps = [
  {
    txt: "Адрес",
    id: 1,
  },
  {
    txt: "Параметры груза",
    id: 2,
  },
  {
    txt: "Тип доставки",
    id: 3,
  },
  {
    txt: "Упаковка",
    id: 4,
  },
  {
    txt: "Данные получателя",
    id: 5,
  },
  {
    txt: "Дата сдачи груза",
    id: 6,
  },
  {
    txt: "Мои данные",
    id: 7,
  },
];
