import {
  Button,
  Dialog,
  DialogContent,
  TextField,
  Typography,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import { Box } from "@mui/system";
import React, { useEffect, useLayoutEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getAccountData } from "../../../../redux/reducers/account";
import { paymentBonus } from "../../../../redux/reducers/ordering";

const DialogBonus = ({ open, setOpen, formik }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const { account } = useSelector(({ account }) => account);
  const firstUpdate = useRef(true);
  const { order_data, payment_data } = useSelector(({ ordering }) => ordering);

  useEffect(() => {
    formik.setFieldValue("amount", formik.values.price - formik.values.sum);
  }, [formik.values.sum]);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    if (payment_data.severity === 200) {
      formik.setFieldValue("price", formik.values.amount);
      dispatch(getAccountData());
    }
  }, [payment_data.severity]);

  return (
    <Dialog
      open={open}
      onClose={() => setOpen(false)}
      scroll="body"
      classes={{ paper: classes.paper }}
    >
      <DialogContent className={classes.DialogContent}>
        <div>
          {" "}
          <Typography variant="h3" mb={3} textAlign="center">
            Оплатить бонусной картой
          </Typography>
          <Typography fontWeight="700" mb={3}>
            Итоговая сумма доставки: {formik.values.price}
          </Typography>
          <TextField
            variant="outlined"
            fullWidth
            name="sum"
            onChange={formik.handleChange}
            value={formik.values.sum}
            placeholder="Введите сумму"
          />
          <Typography color="" mt={1}>
            Вы заплатите наличными/эл.кошельками: {formik.values.amount}
          </Typography>
          <Button
            variant="contained"
            size="large"
            color="primary"
            sx={{ mt: 2 }}
            disabled={formik.values.sum > account.points}
            onClick={() => {
              setOpen(false);
              formik.setFieldValue("bonus", true);
              dispatch(
                paymentBonus({
                  parcel_code: order_data.data.code,
                  amount: formik.values.sum,
                })
              );
            }}
            fullWidth
          >
            Оплатить
          </Button>
        </div>
        <Box>
          <Typography color="#777" mt={3} textAlign="center">
            Ваш баланс бонусной карты: {account.points}
          </Typography>
        </Box>
      </DialogContent>
    </Dialog>
  );
};

export default DialogBonus;

const useStyles = makeStyles(() => ({
  paper: {
    padding: 40,
    maxWidth: "unset",
  },
  // DialogContent: { display: "flex" },
}));
