import React, { useEffect, useState } from "react";
import { TextField, Typography } from "@mui/material";
import { Box } from "@mui/system";

const Sixth = ({ formik }) => {
  const [hours, setHours] = useState(0);
  useEffect(() => {
    if (formik.values.delivery_type === "Авиа") setHours(8);
    else setHours(24);

    const now = new Date();

    const t = new Date(now);
    const u = t.setHours(t.getHours() + hours);
    const date = new Date(u);

    formik.setFieldValue("date", date.toISOString());

    formik.setFieldValue(
      "sending_date",
      `${new Date(date).toLocaleDateString()}, ${new Date(
        date
      ).toLocaleTimeString([], { timeStyle: "short" })}`
    );
  }, [hours]);

  return (
    <>
      <Typography variant="h6">Дата сдачи груза</Typography>
      <Typography
        variant="body2"
        mt={2}
        mb={5}
        lineHeight="150%"
        sx={{
          fontWeight: "400",
        }}
      >
        Время сдачи груза рассчитывается автоматически в зависимости от типа
        доставки. <br />
        "Авиа" - добавляется +8 часов ко времени создания заявки.
        <br />
        "Экспресс" или "Стандарт" - добавляется +24 часа ко времени создания
        заявки.
      </Typography>

      <Box sx={{ width: { xs: "100%", lg: "370px" } }}>
        <TextField
          required
          className={!formik.values.sending_date ? "has-value" : ""}
          placeholder="Дата сдачи груза"
          disabled
          name="sending_date"
          value={formik.values.sending_date}
          onChange={formik.handleChange}
          fullWidth
          type="text"
        />
      </Box>
    </>
  );
};

export default Sixth;
