import { Button, Typography } from "@mui/material";
import { makeStyles } from "@mui/styles";
import React from "react";
import { useDropzone } from "react-dropzone";

const DropZone = ({ setFiles, files, thumbs }) => {
  const classes = useStyles();
  console.log(thumbs);
  const { getRootProps, getInputProps } = useDropzone({
    accept: "image/*",
    multiple: true,
    maxSize: 104857600,

    onDrop: (acceptedFiles) => {
      setFiles(
        acceptedFiles.map((file) =>
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        )
      );
    },
  });
  return (
    <div className={classes.dropzone}>
      <div {...getRootProps({ className: "dropzone" })}>
        <input {...getInputProps()} />
        <Typography variant="body2" color="#9C9C9C" mb={3}>
          PNG, GIF, WEBP, MP4 or MP3, Max 100mb
        </Typography>
        <Button variant="contained" color="primary" size="medium">
          Прикрепить фото
        </Button>
      </div>
      <div className="thumbs" style={{ width: "100%", marginTop: 20 }}>
        {thumbs?.map((item) => (
          <img
            src={item?.props?.src}
            style={{
              width: "200px",
              height: 200,
              borderRadius: "8px",
              objectFit: "cover",
            }}
            alt=""
          />
        ))}
      </div>
    </div>
  );
};

export default DropZone;

const useStyles = makeStyles((theme) => ({
  dropzone: {
    position: "relative",
    border: "1px dashed #444444",
    borderRadius: "8px",
    width: "72%",
    minHeight: "170px",
    display: "flex",
    justifyContent: "center",
    alignItems: "start",
    padding: 27,
    flexWrap: "wrap",

    [theme.breakpoints.down(992)]: {
      width: "100%",
    },

    "& .dropzone": {
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      flexDirection: "column",
    },

    "& .thumbs": {
      width: "100%",
      display: "flex",
      rowGap: 16,
      flexWrap: "wrap",
      columnGap: 16,
    },
  },
}));
